import React, { Component } from 'react';
import { Accordion, Icon } from 'semantic-ui-react';
import '../App.css';

//   
class AccordionExampleStyled extends Component {
state = { activeIndex: 0 }

handleClick = (e, titleProps) => {
const { index } = titleProps
const { activeIndex } = this.state
const newIndex = activeIndex === index ? -1 : index

this.setState({ activeIndex: newIndex })
}

render() {
const { activeIndex } = this.state

return (
    <Accordion styled>
    <Accordion.Title
        active={activeIndex === 0}
        index={0}
        onClick={this.handleClick}
    >
        <Icon name='dropdown' />
          
    </Accordion.Title>
    <Accordion.Content active={activeIndex === 0}>
        <p>
           'To Do list'       .    .  1-3-5    .   . ,        3,       5 .       .          .                    .        .  1-3-5             .   , ,        .         .       . 1-3-5                           .  
        </p>
    </Accordion.Content>

    <Accordion.Title
        active={activeIndex === 1}
        index={1}
        onClick={this.handleClick}
    >
        <Icon name='dropdown' />
           'TO DO LIST'  
    </Accordion.Title>
    <Accordion.Content active={activeIndex === 1}>
        <p>
          'To Do list'     ?      .         .            .    To Do list             .           .         .  ,    .                    . ,           .    .        .          .        .                  . 
        </p>
    </Accordion.Content>

    <Accordion.Title
        active={activeIndex === 2}
        index={2}
        onClick={this.handleClick}
    >
        <Icon name='dropdown' />
        DONE LIST  
    </Accordion.Title>
    <Accordion.Content active={activeIndex === 2}>
        <p>
           To-Do list    . Done list       ,               .        .      .      .       Anti-Todo list .                .                  . To Do list            . Done list      . iDoneThis   Done list   .        .           To Do list           . 
        </p>
    </Accordion.Content>
    <Accordion.Title
        active={activeIndex === 3}
        index={3}
        onClick={this.handleClick}
    >
        <Icon name='dropdown' />
              
    </Accordion.Title>
    <Accordion.Content active={activeIndex === 3}>
        <p>
            Getting Things Done(GTD)       .  . ,    ?        .  Zen to Done(ZTD) ,   GTD       .            .  .     ? ZTD  GTD   .          .       .
        </p>
    </Accordion.Content>
    <Accordion.Title
        active={activeIndex === 4}
        index={4}
        onClick={this.handleClick}
    >
        <Icon name='dropdown' />
        TO-STOP-DOING LIST  
    </Accordion.Title>
    <Accordion.Content active={activeIndex === 4}>
        <p>
        'To Do list'     To-Stop-Doing list.          .                .         .          To-Stop-Doing list  .             To Do list . To-Stop-Doing list                      To-Stop-Doing list  .
        </p>
    </Accordion.Content>
    </Accordion>
)
}
}

function About() {
    return (
    <>
        <h2>About</h2>   
        <div className='imgBox'>
            <img src='./images/about01.jpg' alt='aboutImg'/>
            <div className='imgBoxRight'>
                <h3>Todo-list</h3>
                <p>             . 
                        'To Do list'      . 
                          .                . 
                              .    ? . 
                                   . 
                        .          . 
                                 . 
                                    . 
                        'To Do list'    .       . 
                          .             . 
                       ,    ,      .       . 
                    'To Do list'   ? </p>
            </div>
        </div>
        <AccordionExampleStyled />
    </>
    );
}

export default About;